.trackingOrder_page {
	margin: 40px 0 20px 0;
	padding: 0px 5%;
}
.tracking_order {
	width: 800px;
	margin: 0 auto;
}
.tracking_order--title {
	text-align: center;
	font-size: 28px;
	font-weight: 700;
	color: #000;
}
.tracking_content {
}
.tracking_form {
}
.tracking_form-group {
	margin-bottom: 30px;
}
.tracking_form-group:first-child {
	margin-bottom: 6px;
}
.tracking_form-input {
	width: 100%;
	height: 40px;
	margin-top: 16px;
	padding: 0 12px;
	font-size: 16px;
	border-radius: 2px;
	border: 1px solid #ccc;
	outline: none;
}
.tracking_form-error {
	font-size: 14px;
	color: #e23d06;
	margin-bottom: 4px;
	margin-left: 4px;
	font-weight: 500;
	/* display: none; */
}
.tracking_form-button {
	height: 38px;
	background-color: #f15e2c;
	min-width: 242px;
	border: none;
	outline: none;
	color: white;
	font-weight: 600;
	font-size: 18px;
	/* margin-top: 12px; */
}
.tracking_form-button:hover {
	background-color: #e05729;
	cursor: pointer;
}
.tracking_form-button:focus {
	outline: none;
	border: none;
}

.info_order {
	max-width: 100%;
}
.info_order-header {
	text-align: center;
	font-size: 16px;
	color: #f15e2c;
	border-bottom: 2px solid #000;
}
.info_order-notice {
	display: block;
	margin-bottom: 40px;
}
.info_order-content {
	padding: 30px 0;
}
.status_order {
	border-bottom: 2px dashed #979797;
}
.status_order-heading {
	display: flex;
	justify-content: space-between;
}
.status_order-heading--title {
	font-weight: 700;
	color: #000;
	font-size: 18px;
}
.status_order-heading--title span {
	color: #f15e2c;
}
.status_order-heading--more {
	font-size: 16px;
	color: #808080;
}
.status_order-content {
}
.status_order-content--box {
	display: flex;
}
.status_order-content--title {
	margin: 0px 0px 0px 0px;
	display: flex;
	align-items: center;
	height: 60px;
	width: 25%;
	position: relative;
	display: inline-block;
	text-align: center;
	line-height: 60px;
	transition: all 0.8s;
	font-weight: 500;
	font-size: 18px;
	background-color: #303030;
	color: #fff;
}
.status_order-content--title:last-child {
	/* color: #e05729; */
}
i {
	border: solid white;
	border-width: 0 3px 3px 0;
	display: inline-block;
	padding: 22px;
	position: absolute;
	top: 8px;
	right: 10px;
}
i.right {
	transform: rotate(-45deg);
	-webkit-transform: rotate(-45deg);
}

.status_order-content--detail {
	display: flex;
}
.status_order-content--description {
	height: 120px;
	width: 25%;
	padding: 16px 20px;
	font-size: 10.5px;
}
.status_order-content--description span {
	margin-bottom: 4px;
	display: block;
}
.status_order-content--description p {
	text-align: left;
}
.detail_order {
	padding: 20px 0;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
}
.detail_order-box {
	width: 590px;
	height: auto;
	background-color: #f2f2f2;
	padding: 20px;
	margin-bottom: 34px;
}
.detail_order-box-decription.scroll {
	height: 210px;
	overflow-y: auto;
}
.detail_order-box-heading {
	color: #000;
	font-weight: 700;
	font-size: 20px;
	border-bottom: 2px solid #000;
	box-sizing: border-box;
	padding-bottom: 12px;
	margin-bottom: 0;
}

.detail_order-box-decription {
	padding: 10px 0;
}
.detail_order-box-decription span {
	display: block;
	font-size: 16px;
	color: #808080;
	margin-bottom: 10px;
}
.detail_order-box-product {
	display: flex;
	border-bottom: 1px solid #ccc;
}
.product_order-image {
	width: 182px;
	height: 182px;
}
.product_order-image a {
	display: inline-block;
}
.product_order-image > a > img {
	width: 100%;
}
.product_order-detail {
	flex: 1;
	padding: 0 10px;
}
.product_order-detail > h5 {
	margin: 0;
	font-size: 16px;
	color: #403e3e;
	margin-bottom: 10px;
}
.product_order-detail > h5 > span {
	display: inline;
	margin-left: 8px;
	font-weight: 400;
}
.detail_order-fee {
	border-bottom: 2px dashed #979797;
	padding: 0 0 6px 0;
}
.detail_order-total-price {
	margin-top: 20px;
}
.detail_order-fee-wrapp {
	display: flex;
	justify-content: space-between;
}
.detail_order-fee-wrapp > h5 {
	font-size: 16px;
}

.detail_order-action {
	display: flex;
	justify-content: space-between;
}
.detail_order-btn {
	padding: 10px;
	border-radius: 2px;
	background-color: #f15e2c;
	color: #fff;
	font-weight: 600;
	font-size: 16px;
	margin-right: 8px;
}
.detail_order-btn:hover {
	background-color: #e05729;
	cursor: pointer;
	color: #fff;
}
.active-status {
	color: #e05729;
}
